.search {
  display: grid;
  grid-template-columns: minmax(1rem, 1fr) minmax(20rem, 70rem) minmax(1rem, 1fr);
  margin-bottom: 5rem;
  position: relative;
}

.search_field {
  grid-column: 2/3;
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 2rem;
  border: 0.3rem solid black;
}
.search_field__open {
  border-radius: 2rem 2rem 0 0;
  border-bottom: none;
}

.input {
  width: 100%;
  height: 4rem;
  border: none;
  font-family: "Roboto Light", sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  background: transparent;
}
.input:focus {
  outline: none;
}

.search_icon {
  width: 2.5rem;
  height: 2.5rem;
  margin: 0 1rem 0 1rem;
  z-index: 2;
}

.close_icon {
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 1rem 0 1rem;
  z-index: 2;
}

.dropdown {
  width: 100%;
  background: white;
  border: 0.3rem solid black;
  border-top: none;
  border-radius: 0 0 2rem 2rem;
  display: block;
  grid-column: 2/3;
  position: absolute;
  z-index: 23;
  top: 4rem;
}
.dropdown hr {
  width: 96%;
  margin: 0 auto 1rem auto;
}
.dropdown__menu {
  list-style: none;
}
.dropdown__item {
  font-size: 1.4rem;
  cursor: pointer;
  padding: 0.7rem 1.5rem 0.7rem 1.5rem;
  display: flex;
  flex-direction: column;
}
.dropdown__item:hover, .dropdown__item:focus {
  background: #f8f8f8;
  font-weight: bold;
}
.dropdown__item:last-child {
  padding-bottom: 1rem;
  border-radius: 0 0 2rem 2rem;
}
.dropdown__close {
  display: none;
}
.dropdown a {
  text-decoration: none;
  color: black;
  display: flex;
  flex-direction: row;
}
.dropdown a span {
  font-style: italic;
}

.warning {
  font-size: 1.4rem;
  font-weight: lighter;
  padding: 1.7rem 0 1.7rem 1rem;
  text-align: center;
  font-style: italic;
  color: dimgray;
}

.dark .search_field {
  border: 0.3rem solid white;
}
.dark input {
  color: #fff4e5;
}
.dark input::placeholder {
  color: #dacec2;
}
.dark .dropdown {
  background: #252527;
  border: 0.3rem solid white;
  border-top: none;
}
.dark .dropdown__item:hover {
  background: #484747;
}
.dark .dropdown a {
  color: #fff4e5;
}
.dark .warning {
  color: #dacec2;
}

